body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-menu {
    justify-content: center;
}

.navbar {
    position: fixed;
    width:100%;
    background-color: #262626;
}

a.navbar-item:hover, .link:hover, a:hover {
    background-color: #262626;
    color: #3A9F86
}

.icon, strong, .navbar-item, a {
    color: #FAFAFC;
}

.mobileMenu {
    background-color: #262626;
    padding: 0px
}

.navbar-burger {
    color: #FAFAFC
}

#footer {
    bottom: 0;
    width: 100%;
    padding: 1%;
    background-color: #262626;
    color: #FAFAFC;
}
.project, .projectSection {
    background-color: #EFF3F4;
}

.projectLink {
    color: black;
}

a.card-header-icon:hover, .projectLink:hover {
    color: black;
    background-color: white;
}

.projectCard {
    min-height: 475px;
}

.projectContainer {
    margin-bottom: 2.5em;
    text-align: center;
}
.about {
    background-color: rgba(0,0,0,.1);
    line-height: 1.1;
}

.skills {
    font-size: 3em;
    color: rgba(0,0,0,.4);
    padding: 8px;
    margin: 8px 8px 8px 0;
}

.skillContainer {
    text-align: center;
}

.education {
    text-align: right;
}

.notAnHrCenter, .notAnHrLeft, .notAnHrRight {
    width: 40px;
    height: 1px;
    background-color: rgba(0,0,0,.6);
}

.notAnHrRight {
    float: right;
}

.notAnHrLeft {
    float: left;
}

.notAnHrCenter {
    display: inline-block;
}

.eduContent, .aboutContent {
    margin-top: 2.5em;
}

#skillTitle {
    margin-bottom: 0;
}
canvas {
    position: absolute;
    top: -1%;
    left: -24px;
    max-height: 650px;
}

button {
    position: relative;
    float: right;
}

/* Box would not resize w/o !important, regardless of specificity */
.messageBox {
    min-height: 200px !important;
}

.contactTitle {
    margin-bottom: 2.5em;
}
